.main-container{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.header-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    list-style: none;
    width: 100vw;
    height: 10vh;
    padding: 0;
    margin: 0;
}
.main-container a {
    position: relative;
    text-decoration: none;
    color: #f0f0f0; 
    font-weight: bold;
    overflow: hidden; 
  }
.main-container a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px; 
  background: #007bff;
  transform: scaleX(0); 
  transform-origin: bottom right;
  transition: transform 0.3s ease, opacity 0.3s ease;
  opacity: 0;
}
.main-container a:hover::after {
  transform: scaleX(1); 
  transform-origin: bottom left; 
  opacity: 1; 
}
.body-container{
  width: 100vw;
  height: 90vh;
}