.project-container{
    color: rgb(213, 227, 239);
    width: 100vw;
    height: 90vh;
    display:flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    overflow-y: auto;  
    padding-top: 20px;
}
.project{
    width: 70vw;
    padding: 0px 20px;
    margin-top: 30px;
    border:2px solid wheat;
    border-radius: 20px;
}
.project h4{
    font-size: large;
}
.project h3{
    font-size: medium;
}
.project p{
    font-size: small;
}